.home-page {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  .setting-btn {
    position: absolute;
    top: 10px;
    right: 18px;
  }
  section {
    display: flex;
    flex-direction: column;
    margin-top: 20px;

    .icons8-bitcoin {
      display: inline-block;
      width: 24px;
      height: 24px;
      background: url('data:image/svg+xml;base64,')
        50% 50% no-repeat;
      background-size: 100%;
    }

    & > * {
      display: flex;
      align-items: flex-start;
      gap: 3px;
      padding: 10px;
    }
    .img-preview {
      position: relative;
      margin: 1em auto;
      width: 160px;
      height: 160px;
      background-size: cover;
      background-position: center center;
      border: 1px solid #fff;
      border-radius: 50%;
    }
  }
}
